
<template>
  <div id="app">
    <!--header-->
    <div class="header">
      <mt-header :title="selected" fixed>
        <!--这里需要配置返回到哪里的路由-->
          <mt-button icon="back" @click="goBack" slot="left" v-if="goBackVal">返回</mt-button>

      </mt-header>
    </div>
    <!--跳转到百度-->
    <div class="soso">
      <a href="https://m.baidu.com/?from=1012852s">搜一搜</a>
    </div>
    <!--跳转到百度-->

    <!--header-->

    <!--router-->

      <keep-alive>
        <router-view v-if="loadingPage"></router-view>
      </keep-alive>

    <!--router-->
    <div class="loadPage" v-if="hideVal" >
      <span>加载中</span>
      <mt-spinner :type="2" color="rgb(38, 162, 255)" :size="30"></mt-spinner>
    </div>


    <!--footer-->
    <mt-tabbar v-model="selected" fixed>
      <mt-tab-item id="首页">
        <img slot="icon" src="./assets/1.jpg">
        <span>首页</span>
      </mt-tab-item>
      <mt-tab-item id="发现">
        <img slot="icon" src="./assets/2.jpg">
        <span>发现</span>
      </mt-tab-item>
      <mt-tab-item id="关心">
        <img slot="icon" src="./assets/3.jpg">
        <span>关心</span>
      </mt-tab-item>
      <mt-tab-item id="我的">
        <img slot="icon" src="./assets/0.jpg">
        <span>我的</span>
      </mt-tab-item>
    </mt-tabbar>
    <!--footer-->
  </div>
</template>

<script>
  import Vue from 'vue'
  import {Tabbar, TabItem,Spinner,Header} from 'mint-ui';

  Vue.component(Spinner.name, Spinner);
  Vue.component(Tabbar.name, Tabbar);
  Vue.component(Header.name, Header);
  Vue.component(TabItem.name, TabItem);


  export default {
    name: 'app',
    data() {
      return {
        selected: '首页',
        loadingPage:false,
        hideVal:true
      }
    },
    created() {
      this.$store.dispatch('getDatas')
    },

    computed: {
      nbaData() {
        return this.$store.state.nbaData
      },
      goBackVal(){
          return this.$store.state.goBackVal
      },
    },
    mounted(){
        this.loading( )
    },
    methods: {
        goBack () {
            this.$router.go(-1)
        },
      loading(  ){


            setTimeout(() => {

              this.loadingPage = true
              this.hideVal = false

          },2000)
        }


    },
    watch: {
      selected() {
        let routerVal = '';
        if (this.selected == '首页') {
          routerVal = '/home'
        } else if (this.selected == '发现') {
          routerVal = '/find'
        } else if (this.selected == '关心') {
          routerVal = '/concern'
        } else if (this.selected == '我的') {
          routerVal = '/mine'
        }
        this.$router.push({path: routerVal})
      },

    },
  }
</script>

<style scoped>
  .header{
    z-index: 999;
  }
  .mint-header {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    background-color: #26a2ff;
    box-sizing: border-box;
    color: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 14px;
    height: 40px;
    line-height: 1;
    padding: 0 10px;
    position: relative;
    text-align: center;
    white-space: nowrap;
  }

  .mint-header .mint-button {
    background-color: transparent;
    border: 0;
    box-shadow: none;
    color: inherit;
    display: inline-block;
    padding: 0;
    font-size: inherit
  }

  .mint-header .mint-button::after {
    content: none;
  }

  .mint-header.is-fixed {
    top: 0;
    right: 0;
    left: 0;
    position: fixed;
    z-index: 1;
  }

  .mint-header-button {
    -webkit-box-flex: .5;
    -ms-flex: .5;
    flex: .5;
  }

  .mint-header-button > a {
    color: inherit;
  }

  .mint-header-button.is-right {
    text-align: right;
  }

  .mint-header-button.is-left {
    text-align: left;
  }

  .mint-header-title {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: inherit;
    font-weight: 400;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
  }

  .soso {
    width: 130px;
    border-radius: 20px;
    font-size: 30px;
    background: #ffffff;
    position: fixed;
    top: 9px;
    right: 13px;
    text-align: center;
    line-height: 60px;
    z-index: 999;
  }

  .soso a {
    text-decoration: none;
    color: #0a66ff;
  }

  .mint-tab-item span {
    font-size: 30px;
    box-sizing: border-box;
  }

  .mint-tabbar {
    border-top: 1px solid #ccc;
  }

  .mint-header {

    font-size: 35px;
    height: 80px;

  }
.loadPage{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 500px;
  height: 300px;
  margin: 100px auto;
}
  .loadPage span{
    color: #000;
    font-size: 30px;
    margin-left: 20px;
  }

</style>



