
import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home/Home'
import Find from '@/components/Find/Find'
import Concern from '@/components/Concern/Concern'
import Mine from '@/components/Mine/Mine'
import News1 from '@/components/Home/News/News1'
import News2 from '@/components/Home/News/News2'
import News3 from '@/components/Home/News/News3'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
   /* {
      path: '/home',
      redirect: '/home/news1'
    },*/
    {
      path:'/home',
      name:'Home',
  component:Home,
 /* children:[
  {
    path:'news1',
    name:'News1',
    component:News1
  },
  {
    path:'news2',
    name:'News2',
    component:News2
  },
  {
    path:'news3',
    name:'News3',
    component:News3
  }
      ]*/
    },
    {
      path:'/find',
      name:'Find',
      component:Find
    },
    {
      path:'/concern',
      name:'Concern',
      component:Concern
    },
    {
      path:'/mine',
      name:'Mine',
      component:Mine
    }
  ]
})



